'use strict';
angular.module('videoHub')
  .directive('tagsAutocomplete', function($injector) {
    return {
      templateUrl: 'shared/tags-autocomplete/tags-autocomplete.html',
      scope: {
        'items': '=',
        'service': '@service',
        'labelAttr': '@',
        'placeholderText': '@'
      },
      controller: function($scope, AlertService) {
        //Inject service based on provided value
        var service = $injector.get($scope.service);
        //Debounce the search to avoid spamming requests
        $scope.$watch('search', function() {
          $scope.fetchItemsThatMatchSearch();
        });

        $scope.fetchItemsThatMatchSearch = function(){
          if (_.isEmpty($scope.search)) {
            $scope.currentItems = [];
          } else {
            $scope.tagsAreBeingFetchedFromTheServer = true;
            //TODO Make this configurable
            AlertService.info('Tags are being fetched from the server, please wait.', false);
            service.getList({
              'q': $scope.search
            }).then($scope.updateCurrentItemsAndDisplayDropdown,
              function() {
                AlertService.error('Somethign went wrong. Please contact your friendly sysadmin', true);
              });
          }
        };

        $scope.updateCurrentItemsAndDisplayDropdown = function(data) {
          $scope.tagsAreBeingFetchedFromTheServer = false;
          AlertService.success('Tags successfully fetched', true);
          if (!_.isUndefined(data)) {
            var currentItemsIds = _.pluck($scope.viewValues, 'id');
            data = _.reject(data, function(item) {
              return _.contains(currentItemsIds, item.id);
            });
            $scope.currentItems = data;
          } else {
            $scope.currentItems = null;
          }
        };

        $scope.clearSearch = function() {
          $scope.search = '';
          $scope.currentItems = [];
          $scope.fetchItemsThatMatchSearch();
          $scope.selectedElement = -1;
        };

        $scope.selectedElement = -1;
        $scope.controlSelectedElementWithKeyboard = function(event) {
          var keyCode = event.keyCode;
          if ($scope.currentItems && !_.isEmpty($scope.search)) {
            var lastIndexOfCurrentItems = $scope.currentItems.length - 1;
            switch (keyCode) {
              case 27: // ESC
                $scope.clearSearch();
                break;
              case 40: // DOWN
                $scope.selectedElement = $scope.selectedElement >= lastIndexOfCurrentItems ?
                  0 :
                  $scope.selectedElement + 1;
                break;
              case 38: // UP
                $scope.selectedElement = $scope.selectedElement <= 0 ?
                  lastIndexOfCurrentItems :
                  $scope.selectedElement - 1;
                break;
              case 13: // RETURN
                if (!$scope.tagsAreBeingFetchedFromTheServer) {
                  $scope.addItem($scope.currentItems[$scope.selectedElement]);
                  $scope.selectedElement = -1;
                }
                break;
              default:
                return;
            }
          }

        };

        $scope.addItem = function(item) {
          if (_.isUndefined(item)) {
            item = {
              'name': $scope.search
            };
            var isCurrentTagInCurrentItemsPredicate = _.matches(item);
            var currentObjectFetchedFromCurrentItems = _.find($scope.currentItems, isCurrentTagInCurrentItemsPredicate);
            if (currentObjectFetchedFromCurrentItems) {
              $scope.viewValues.push(currentObjectFetchedFromCurrentItems);
            } else {
              AlertService.info('Saving new tag...', false);
              service.post(item).then(function(data) {
                $scope.viewValues.push(data);
                $scope.items = _.pluck($scope.viewValues, 'id');
                $scope.clearSearch();
                AlertService.success('New tag saved', true);
              }, function() {
                AlertService.error('Something went wrong. Plase contact your friendly sysadmin.', true);
              });
            }
          } else {
            $scope.viewValues.push(item);
          }
          $scope.items = _.pluck($scope.viewValues, 'id');
          $scope.clearSearch();
        };

        $scope.removeItem = function(itemToBeRemovedFromArray) {
          $scope.viewValues = _.reject($scope.viewValues, function(item) {
            return item.id === itemToBeRemovedFromArray.id;
          });
          $scope.items = _.pluck($scope.viewValues, 'id');
        };

        if ($scope.items) {
          $scope.viewValues = $scope.items;
          $scope.items = _.pluck($scope.items, 'id');
        } else {
          $scope.viewValues = [];
          $scope.items = [];
        }
      }
    };
  });
